@import url("https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap");

/* Used for dot dot dot animation */
.loading:after {
    content: ' .';
    animation: dots 1s steps(5, end) infinite;
    font-family: 'Press Start 2P', cursive;
    font-size: large;
}
  
  @keyframes dots {
    0%, 20% {
      color: rgba(0,0,0,0);
      text-shadow:
        .25em 0 0 rgba(0,0,0,0),
        .5em 0 0 rgba(0,0,0,0);}
    40% {
      color: white;
      text-shadow:
        .25em 0 0 rgba(0,0,0,0),
        .5em 0 0 rgba(0,0,0,0);}
    60% {
      text-shadow:
        .25em 0 0 white,
        .5em 0 0 rgba(0,0,0,0);}
    80%, 100% {
      text-shadow:
        .25em 0 0 white,
        .5em 0 0 white;}
    }

